.item {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 20px;
}


.item__box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  padding: 10px 0;
}

.item__img-box {
  margin-right: 40px;
}

.item__img {
  width: 145px;
  height: auto;
}

.item__title {
  margin: 0;
  margin-bottom: 5px;
  color: var(--blue);
  font-size: 20px;
}


.item__price {
  font-weight: bold;
  margin-top: auto;
}

.item__btn {
  margin-top: 10px;
}
@keyframes lds-rolling {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@-webkit-keyframes lds-rolling {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.spinner {
  -webkit-align-self: center;
          align-self: center;
  margin: 0 auto;
}

.lds-rolling {
  position: relative;
}
.lds-rolling div,
.lds-rolling div:after {
  position: absolute;
  width: 160px;
  height: 160px;
  border: 20px solid var(--blue);
  border-top-color: transparent;
  border-radius: 50%;
}
.lds-rolling div {
  -webkit-animation: lds-rolling 1s linear infinite;
  animation: lds-rolling 1s linear infinite;
  top: 100px;
  left: 100px;
}
.lds-rolling div:after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.lds-rolling {
  width: 100px;
  height: 100px;
  -webkit-transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
  transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
}
.error-indicator {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: #c78f22;
}

.error-indicator img {
  margin-bottom: 1rem;
}

.error-indicator .boom {
  font-size: 1.7rem;
}
.item-list {
  margin-bottom: 50px;
}

.item-list__list {
  margin: 0;
  list-style: none;
}
.cart-table__title {
  margin: 0;
  padding-left: 20px;
  margin-bottom: 5px;
}

.cart-table__table {
  width: 100%;
  text-align: left;
  border-collapse: collapse;
  border: none;
  margin-bottom: 10px;
}

.cart-table__table thead th {
  padding: 10px 0;
  border-top: 1px solid var(--border-color);
  border-bottom: 1px solid var(--border-color);
}

.cart-table__table td {
  padding: 10px 0;
}

.cart-table__table .btn {
  margin-right: 2px;
  padding: 5px 10px;
}

.cart-table__total {
  text-align: right;
}
.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 40px;
  padding: 0 20px;
  border-bottom: 1px solid var(--border-color);
}

.header__logo {
  text-decoration: none;
}

.header__logo:hover {
  color: var(--blue);
}

.header__cart-icon {
  color: cadetblue;
}

.header__logo-link {
  font-family: 'Playfair Display', sans-serif;
  font-size: 40px;
  font-weight: 700;
  color: inherit;
  text-decoration: none;
}

.header__cart-link {
  color: inherit;
  font: inherit;
  text-decoration: none;
}

.header__cart-link:hover {
  text-decoration: underline;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

:root {
  --dark-blue: #0069D9;
  --blue: #007bff;
  --light-gray: #dddddd;
  --border-color: var(--light-gray);
}

body {
  margin: 0;
  min-width: 320px;
  font-size: 20px;
  font-family: "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

.btn {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  color: inherit;
  font: inherit;
  text-align: left;
  cursor: pointer;
}

.btn-blue {
  background-color: cadetblue;
  color: #ffffff;
}

.btn-blue:hover,
.btn-blue:focus {
  background-color: #5f9eb5;
}

.btn-trash {
  border: 1px solid red;
  color: red;
}

.btn-trash:hover {
  background-color: red;
  color: #ffffff;
}

.btn-minus {
  border: 1px solid #eec600;
  color: #eec600;
}

.btn-minus:hover {
  background-color: #eec600;
  color: #ffffff;
}

.btn-plus {
  border: 1px solid green;
  color: green;
}

.btn-plus:hover {
  background-color: green;
  color: #ffffff;
}

@media  (min-width: 1550px) {
  .container {
    width: 1550px;
    margin: 0 auto;
  }
}
